<template>
  <div>
    <header-bar title="title"></header-bar>
    <div class="content">
      <div class="week-number">
        <i class="ivu-icon ivu-icon-md-calendar"></i>
        <p>{{'Week ' + week.number}}</p>
      </div>
      <div class="week-days">
        <template v-for="(item, index) in week.weekdays" @click="weekDayClick(item)">
          <div
            :key="index"
            @click="weekDayClick(item)"
            class="week-day-item"
            :class="{selected: isSelectedWeekDay(item)}"
          >
            <Row>
              <Col span="12">
                <Row>
                  <div class="title">
                    <template v-if="!isToday(item)">
                      <span>
                        <strong>{{item.date | format('ddd')}}</strong>
                      </span>
                    </template>
                    <Badge dot v-if="isToday(item)">
                      <span>
                        <strong>{{item.date | format('ddd')}}</strong>
                      </span>
                    </Badge>
                  </div>
                </Row>
                <Row>
                  <div class="sub-title">
                    <span>{{item.date | format('DD/MM/YYYY')}}</span>
                  </div>
                </Row>
              </Col>
              <Col span="12" style="text-align: right">
                <Row>
                  <TimePicker
                    :open="item.openCheckInTimePicker"
                    :value="item.checkIn && item.checkIn.toDate()"
                    :steps="[1,5]"
                    format="HH:mm"
                    @on-change="time => handleTimeChange(time, 'checkIn')"
                    @on-ok="item.openCheckInTimePicker = false"
                    placement="bottom-end"
                    confirm
                  >
                    <a href="javascript:void(0)" @click="item.openCheckInTimePicker = true">
                      <template>
                        <span
                          v-if="item.checkIn"
                          style="color: #19be6b"
                        >{{item.checkIn | format("HH:mm")}}</span>
                      </template>
                    </a>
                  </TimePicker>
                  <span v-if="item.checkOut" style="margin: 0 5px;">-</span>
                  <TimePicker
                    :open="item.openCheckOutTimePicker"
                    :value="item.checkOut && item.checkOut.toDate()"
                    :steps="[1,5]"
                    format="HH:mm"
                    @on-change="time => handleTimeChange(time, 'checkOut')"
                    @on-ok="item.openCheckOutTimePicker = false"
                    placement="bottom-end"
                    confirm
                  >
                    <a href="javascript:void(0)" @click="item.openCheckOutTimePicker = true">
                      <template>
                        <span
                          v-if="item.checkOut"
                          style="color: #ed4014"
                        >{{item.checkOut | format("HH:mm")}}</span>
                      </template>
                    </a>
                  </TimePicker>
                </Row>
                <Row>
                  <strong>{{item.duration}}</strong>
                </Row>
              </Col>
            </Row>
          </div>
        </template>
      </div>

      <div class="fixed-bottom" v-if="selectedWeekDay">
        <Button type="success" long v-if="!selectedWeekDay.checkIn" @click="checkIn">CHECK IN</Button>
        <Button
          type="error"
          long
          v-if="selectedWeekDay.checkIn && !selectedWeekDay.checkOut"
          @click="checkOut"
        >CHECK OUT</Button>
        <Button
          type="warning"
          long
          v-if="selectedWeekDay.checkIn && selectedWeekDay.checkOut"
          @click="reset"
        >RESET</Button>
      </div>
    </div>
    <!-- <pre>{{week}}</pre> -->
  </div>
</template>


<script>
import HeaderBar from "./HeaderBar.vue";
var delayFunction = null;
export default {
  name: "Week",
  components: { HeaderBar },
  props: {},
  data: function() {
    return {
      week: {},
      today: {},
      selectedWeekDay: undefined
    };
  },
  mounted: function() {
    this.today = this.$moment();

    this.week = this.getWeek()
      .then(w => {
        this.convertToMomentDate(w);
        return w;
      })
      .then(w => (this.week = w))
      .then(() => {
        var index = this.week.weekdays.findIndex(x => x.dayOfYear == this.today.dayOfYear());
        if (index > -1) {
          this.selectedWeekDay = this.week.weekdays[index];
        }
      });
  },
  computed: {},
  methods: {
    getWeek: function(weekNum) {
      if (!weekNum) {
        weekNum = this.$moment().week();
        //sunday
        if (this.today.day() == 0) {
          weekNum--;
        }
      }

      return this.$fb.weekCollection
        .where("number", "==", weekNum)
        .limit(1)
        .get()
        .then(ss => {
          if (ss.empty) return this.buildNewWeek(weekNum);
          else {
            return ss.docs[0].data();
          }
        });
      //get Week data here
    },
    buildNewWeek: function(weekNum) {
      var currentWeek = this.$moment().week(weekNum);
      const startDate = this.$moment(currentWeek)
        .startOf("week")
        .add("d", 1);
      const endDate = this.$moment(currentWeek)
        .endOf("week")
        .add("d", 1);

      var week = {
        number: weekNum,
        startDate: startDate,
        endDate: endDate,
        weekdays: []
      };

      for (var i = 0; i < 7; i++) {
        var date = this.$moment(startDate).add("d", i);
        var dayOfYear = date.dayOfYear();
        var dayOfWeek = date.days();
        week.weekdays.push({
          date: date,
          dayOfYear: dayOfYear,
          dayOfWeek: dayOfWeek,
          checkIn: "",
          checkOut: "",
          duration: "",
          openCheckInTimePicker: false,
          openCheckOutTimePicker: false
        });
      }

      return week;
    },
    isToday: function(item) {
      return this.today.isSame(item.date, "day");
    },
    weekDayClick: function(item) {
      this.selectedWeekDay = item;
    },
    isSelectedWeekDay: function(item) {
      return this.selectedWeekDay && this.selectedWeekDay.dayOfWeek == item.dayOfWeek;
    },
    checkIn: function() {
      this.selectedWeekDay.checkIn = this.$moment();
      this.updateWeekDay();
    },
    checkOut: function() {
      this.selectedWeekDay.checkOut = this.$moment();
      this.updateWeekDay();
    },
    reset: function() {
      this.$Modal.confirm({
        title: "Confirmation",
        content: `Are you sure you want to reset timesheet on ${this.$options.filters.format(
          this.selectedWeekDay.date,
          "ddd DD/MM/YYYY"
        )} ?`,
        onOk: () => {
          this.selectedWeekDay.checkIn = "";
          this.selectedWeekDay.checkOut = "";
          this.updateWeekDay();
        },
        onCancel: () => {}
      });
    },
    updateWeekDay: function() {
      if (delayFunction) window.clearTimeout(delayFunction);
      delayFunction = window.setTimeout(() => {
        if (this.selectedWeekDay.checkIn && this.selectedWeekDay.checkOut) {
          var duration = this.$moment.duration(
            this.selectedWeekDay.checkOut.diff(this.selectedWeekDay.checkIn)
          );
          this.selectedWeekDay.duration = (Math.round(duration.as("hours") * 100) / 100).toFixed(2);
        } else {
          this.selectedWeekDay.duration = "";
        }
        var index = this.week.weekdays.findIndex(
          x => x.dayOfYear == this.selectedWeekDay.dayOfYear
        );
        this.week.weekdays.splice(index, 1, this.selectedWeekDay);

        var clonedWeek = {};
        if (!this.week.id) {
          var weekRef = this.$fb.weekCollection.doc();
          this.week.id = weekRef.id;

          clonedWeek = this.buildFSWeek();

          return weekRef.set(clonedWeek).then(() => this.$Message.info("Timesheet saved!"));
        } else {
          clonedWeek = this.buildFSWeek();
          this.$fb.weekCollection
            .doc(clonedWeek.id)
            .set(clonedWeek)
            .then(() => this.$Message.info("Timesheet saved!"));
        }
      }, 500);
    },
    buildFSWeek: function() {
      var clonedWeek = {};
      Object.assign(clonedWeek, this.week);

      clonedWeek.startDate = clonedWeek.startDate && clonedWeek.startDate.toDate();
      clonedWeek.endDate = clonedWeek.endDate && clonedWeek.endDate.toDate();
      clonedWeek.weekdays = [];

      this.week.weekdays.forEach(x => {
        clonedWeek.weekdays.push({
          date: x.date && x.date.toDate(),
          checkIn: x.checkIn && x.checkIn.toDate(),
          checkOut: x.checkOut && x.checkOut.toDate(),
          duration: x.duration || "",
          dayOfWeek: x.dayOfWeek,
          dayOfYear: x.dayOfYear
        });
      });
      return clonedWeek;
    },
    convertToMomentDate: function(week) {
      week.startDate = this.$moment(week.startDate.toDate());
      week.endDate = this.$moment(week.endDate.toDate());
      week.weekdays.forEach(x => {
        x.date = this.$moment(x.date.toDate());
        x.checkIn = x.checkIn && this.$moment(x.checkIn.toDate());
        x.checkOut = x.checkOut && this.$moment(x.checkOut.toDate());
        x.openCheckInTimePicker = false;
        x.openCheckOutTimePicker = false;
      });
    },
    handleTimeChange: function(time, type) {
      var timeParts = time.split(":");
      var hours = timeParts[0];
      var minutes = timeParts[1];
      if (type == "checkIn") {
        this.selectedWeekDay.checkIn.set("hour", hours).set("minute", minutes);
      } else {
        this.selectedWeekDay.checkOut.set("hour", hours).set("minute", minutes);
      }
      this.updateWeekDay();
    }
  }
};
</script>

