import Vue from 'vue'
import Router from 'vue-router'

import Home from "./components/Home"
import Dashboard from "./components/Dashboard"
import Week from "./components/Week"

import Login from "./components/Login"

Vue.use(Router)

function configRoutes() {
    return [
        {
            path: '/',
            redirect: '/week',
            name: 'Home',
            component: Home,
            meta: {
                requiresAuth: true
            },
            children: [
                {
                    path: 'dashboard',
                    name: 'Dashboard',
                    component: Dashboard,
                    meta: {
                        title: "Dashboard"
                    }
                },
                {
                    path: 'week',
                    name: 'Week',
                    component: Week,
                    meta: {
                        title: "Week"
                    }
                }
            ]
        },
        {
            path: '/login',
            name: 'Login',
            component: Login,
            meta: {
                requiresAuth: false,
                title: "Login"
            }
        }
    ]
}

const router = new Router({
    mode: 'hash', // https://router.vuejs.org/api/#mode
    linkActiveClass: 'open active',
    scrollBehavior: () => ({ y: 0 }),
    routes: configRoutes()
});


export default router;
